import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '../tools/withStyles';
import { Main } from '../components/Main';
import { Text } from '../components/Text';
import { Animation } from '../components/Animation';
import anime from 'animejs';

const styles = theme => ({
  root: {
    position: 'relative',
    padding: 20
  },
  terminal: {
    background: theme.color.background.dark,
    border: `1px solid ${theme.color.secondary.main}`,
    borderRadius: 8,
    padding: 20,
    maxWidth: 800,
    margin: '0 auto',
    boxShadow: `0 0 20px ${theme.color.secondary.main}33`
  },
  terminalHeader: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: 20,
    padding: '10px 0',
    borderBottom: `1px solid ${theme.color.secondary.main}33`
  },
  terminalButton: {
    width: 12,
    height: 12,
    borderRadius: '50%',
    marginRight: 8,
    '&.red': {
      background: '#ff5f56',
      border: '1px solid #e0443e'
    },
    '&.yellow': {
      background: '#ffbd2e',
      border: '1px solid #dea123'
    },
    '&.green': {
      background: '#27c93f',
      border: '1px solid #1aab29'
    }
  },
  loadingBar: {
    height: 2,
    background: `linear-gradient(90deg, 
      ${theme.color.secondary.main} 0%, 
      ${theme.color.secondary.main}33 50%, 
      ${theme.color.secondary.main} 100%)`,
    backgroundSize: '200% 100%',
    animation: '$moveGradient 2s linear infinite',
    marginBottom: 20
  },
  '@keyframes moveGradient': {
    '0%': { backgroundPosition: '100% 0%' },
    '100%': { backgroundPosition: '-100% 0%' }
  },
  message: {
    fontFamily: 'monospace',
    color: theme.color.secondary.main,
    margin: '10px 0',
    opacity: 0,
    transform: 'translateY(10px)',
    '&.visible': {
      opacity: 1,
      transform: 'translateY(0)',
      transition: 'all 0.3s ease-out'
    }
  },
  cursor: {
    display: 'inline-block',
    width: 8,
    height: 15,
    background: theme.color.secondary.main,
    marginLeft: 5,
    animation: '$blink 1s infinite'
  },
  '@keyframes blink': {
    '0%, 100%': { opacity: 1 },
    '50%': { opacity: 0 }
  },
  progressContainer: {
    marginTop: 20,
    padding: 10,
    border: `1px solid ${theme.color.secondary.main}33`,
    borderRadius: 4
  },
  progressLabel: {
    display: 'flex',
    justifyContent: 'space-between',
    marginBottom: 5,
    color: theme.color.secondary.main
  },
  progressBar: {
    height: 4,
    background: theme.color.background.light,
    borderRadius: 2,
    overflow: 'hidden'
  },
  progressFill: {
    height: '100%',
    background: theme.color.secondary.main,
    width: '0%',
    transition: 'width 0.5s ease-out'
  }
});

class Blog extends React.Component {
  state = {
    messages: [
      "Initializing cybersecurity protocols...",
      "Loading penetration testing modules...",
      "Scanning for vulnerabilities in the matrix...",
      "ERROR: Coffee levels critically low â˜•",
      "Compiling hacker knowledge base...",
      "WARNING: Sleep.exe not found in system32",
      "npm install more-brain-power@latest",
      "Debugging life choices...",
      "Loading CTF solutions...",
      "ERROR: Work-life balance not found",
      "Importing OSCP memories...",
      "Testing firewall against emotional damage",
      "Loading progress... Like my career, it's getting there",
      "Site will be ready after I hack the Gibson",
      "Or maybe after I finish this CTF... just one more box"
    ],
    currentMessageIndex: 0,
    progress: 0
  };

  componentDidMount() {
    this.startMessageAnimation();
    this.startProgressAnimation();
  }

  componentWillUnmount() {
    clearInterval(this.messageInterval);
    clearInterval(this.progressInterval);
  }

  startMessageAnimation = () => {
    this.messageInterval = setInterval(() => {
      const { currentMessageIndex, messages } = this.state;
      if (currentMessageIndex < messages.length - 1) {
        this.setState({ 
          currentMessageIndex: currentMessageIndex + 1 
        }, () => {
          this.animateMessage(currentMessageIndex + 1);
        });
      }
    }, 2000);
  };

  startProgressAnimation = () => {
    this.progressInterval = setInterval(() => {
      this.setState(prev => ({
        progress: Math.min(prev.progress + 1, 99)
      }));
    }, 500);
  };

  animateMessage = (index) => {
    anime({
      targets: `.message-${index}`,
      opacity: [0, 1],
      translateY: [10, 0],
      easing: 'easeOutExpo',
      duration: 1000
    });
  };

  render() {
    const { classes } = this.props;
    const { messages, currentMessageIndex, progress } = this.state;

    return (
      <Main className={classes.root}>
        <Animation show>
          <div className={classes.terminal}>
            <div className={classes.terminalHeader}>
              <div className={`${classes.terminalButton} red`} />
              <div className={`${classes.terminalButton} yellow`} />
              <div className={`${classes.terminalButton} green`} />
            </div>
            <div className={classes.loadingBar} />
            
            {messages.slice(0, currentMessageIndex + 1).map((message, index) => (
              <div 
                key={index}
                className={`${classes.message} message-${index}`}
              >
                <Text>{`> ${message}`}</Text>
              </div>
            ))}
            
            <div className={classes.progressContainer}>
              <div className={classes.progressLabel}>
                <Text>Site Development Progress</Text>
                <Text>{`${progress}%`}</Text>
              </div>
              <div className={classes.progressBar}>
                <div 
                  className={classes.progressFill} 
                  style={{ width: `${progress}%` }} 
                />
              </div>
            </div>
          </div>
        </Animation>
      </Main>
    );
  }
}

export default withStyles(styles)(Blog);
